/*  */
.container {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #f7f7f5;
  overflow: hidden;
}

/*  */
.headerImage {
  width: 100%;
  height: 140px;
  overflow: hidden;
  position: relative;
  background: linear-gradient(135deg, #F1F1E8 0%, #B9C9B9 100%);
  flex-shrink: 0;
}

.headerImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/*  */
.backButton {
  position: absolute;
  top: 12px;
  left: 12px;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: none;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: rgb(55, 53, 47);
  transition: all 0.2s;
  z-index: 10;
  /*  */
  border: 1px solid rgba(55, 53, 47, 0.08);
}

.backButton:hover {
  background: white;
  transform: scale(1.05);
}

.backButton:active {
  transform: scale(0.95);
}

/*  */
.rightAction {
  position: absolute;
  top: 12px;
  right: 12px;
  z-index: 10;
}

/*  */
.titleSection {
  padding: 16px 20px 12px;
  background: white;
  border-bottom: 1px solid rgba(55, 53, 47, 0.06);
  flex-shrink: 0;
}

.title {
  font-size: 17px;
  font-weight: 600;
  color: rgb(55, 53, 47);
  margin: 0 0 4px;
}

.description {
  font-size: 13px;
  color: rgba(55, 53, 47, 0.5);
  margin: 0;
  line-height: 1.4;
}

/*  - overflow */
.content {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
