/*  */
.content {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding: 0;
}

/*  -  */
.tagSettingsWrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/*  TagSettings  */
.tagSettingsWrapper :global(.tag-settings-header) {
  display: none;
}

/*  TagSettings  */
.tagSettingsWrapper :global(.tag-settings) {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding-top: 0;
}

/*  */
.tagSettingsWrapper :global(.tag-settings-list-container) {
  flex: 1;
  overflow: hidden;
}

/*  */
.tagSettingsWrapper :global(.tag-settings-list) {
  height: 100%;
  overflow-y: auto;
}
