/* CycleSettingsPage  */
.container {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.content {
  flex: 1;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  padding-top: 8px;
  padding-bottom: 100px;
}

/*  */
.dateSection {
  padding: 0 16px;
  margin-bottom: 16px;
}

.sectionTitle {
  font-size: 13px;
  font-weight: 500;
  color: rgba(55, 53, 47, 0.5);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 10px;
  padding-left: 4px;
}

.dateInputWrapper {
  display: flex;
  align-items: center;
  gap: 12px;
  background: white;
  border-radius: 12px;
  padding: 14px 16px;
  cursor: pointer;
  transition: background 0.15s;
}

.dateInputWrapper:active {
  background: rgba(55, 53, 47, 0.04);
}

.dateIcon {
  color: rgba(55, 53, 47, 0.5);
  flex-shrink: 0;
}

.dateValue {
  flex: 1;
  font-size: 15px;
  color: rgb(55, 53, 47);
}

.dateHint {
  font-size: 12px;
  color: rgba(55, 53, 47, 0.5);
  margin-top: 8px;
  padding-left: 4px;
}

/*  */
.costHint {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  color: rgb(55, 53, 47);
  font-weight: 500;
}

.costIcon {
  width: 18px;
  height: 18px;
  object-fit: contain;
}

/*  */
.originalCost {
  text-decoration: line-through;
  color: rgba(55, 53, 47, 0.45);
  font-weight: 400;
  margin-right: 4px;
}

/*  */
.discountedCost {
  color: #e74c3c;
  font-weight: 600;
}
