/* MessageBubble  - Smore  */

.bubble {
  max-width: 85%;
  margin-bottom: 14px;
  animation: fadeIn 0.25s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ====================  -  ==================== */
.user {
  margin-left: auto;
}

.user .content {
  /*  */
  color: rgb(55, 53, 47);
  border-radius: 22px;
  border-bottom-right-radius: 8px;
  padding: 12px 18px;
  font-size: 15px;
  line-height: 1.5;
  word-break: break-word;
}

/* ==================== AI  -  ==================== */
.assistant {
  margin-right: auto;
  max-width: 92%;
}

.assistant .content {
  background: rgba(255, 255, 255, 0.75);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  color: rgb(55, 53, 47);
  border-radius: 18px;
  border-bottom-left-radius: 6px;
  padding: 14px 18px;
  font-size: 15px;
  line-height: 1.7;
  word-break: break-word;
  border: 1px solid rgba(255, 255, 255, 0.5);
}

/*  */
.cursor {
  display: inline-block;
  animation: blink 0.8s infinite;
  color: rgba(55, 53, 47, 0.4);
  margin-left: 2px;
}

@keyframes blink {
  0%, 50% {
    opacity: 1;
  }
  51%, 100% {
    opacity: 0;
  }
}

/*  */
.errorHint {
  font-size: 12px;
  color: #e03e3e;
  margin-top: 4px;
}

/* Markdown  - Notion  */
.markdown {
  font-size: 15px;
  line-height: 1.7;
  color: rgb(55, 53, 47);
}

/*  */
.markdown :global(p) {
  margin: 0 0 12px;
}

.markdown :global(p:last-child) {
  margin-bottom: 0;
}

/* / */
.markdown :global(ul),
.markdown :global(ol) {
  margin: 10px 0;
  padding-left: 24px;
}

.markdown :global(ol) {
  list-style-type: decimal;
}

.markdown :global(li) {
  margin: 6px 0;
  padding-left: 4px;
}

/*  */
.markdown :global(li p) {
  margin: 4px 0;
}

.markdown :global(li p:first-child) {
  margin-top: 0;
}

.markdown :global(li p:last-child) {
  margin-bottom: 0;
}

/*  */
.markdown :global(li ul),
.markdown :global(li ol) {
  margin: 6px 0;
}

/*  */
.markdown :global(code) {
  background: rgba(55, 53, 47, 0.06);
  padding: 2px 6px;
  border-radius: 4px;
  font-family: 'SF Mono', Monaco, 'Consolas', monospace;
  font-size: 13px;
  color: #eb5757;
}

/*  */
.markdown :global(pre) {
  background: rgba(55, 53, 47, 0.05);
  padding: 14px 16px;
  border-radius: 8px;
  overflow-x: auto;
  margin: 12px 0;
}

.markdown :global(pre code) {
  background: none;
  padding: 0;
  color: rgb(55, 53, 47);
  font-size: 13px;
  line-height: 1.5;
}

/*  */
.markdown :global(blockquote) {
  border-left: 3px solid rgba(55, 53, 47, 0.2);
  margin: 12px 0;
  padding: 8px 0 8px 16px;
  color: rgba(55, 53, 47, 0.7);
  background: rgba(55, 53, 47, 0.02);
  border-radius: 0 4px 4px 0;
}

.markdown :global(blockquote p) {
  margin: 0;
}

/*  */
.markdown :global(strong) {
  font-weight: 600;
  color: rgb(55, 53, 47);
}

.markdown :global(em) {
  font-style: italic;
}

/*  */
.markdown :global(a) {
  color: #2383e2;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: border-color 0.2s;
}

.markdown :global(a:hover) {
  border-bottom-color: #2383e2;
}

/*  */
.markdown :global(h1),
.markdown :global(h2),
.markdown :global(h3) {
  font-weight: 600;
  color: rgb(55, 53, 47);
  margin: 16px 0 10px;
}

.markdown :global(h1:first-child),
.markdown :global(h2:first-child),
.markdown :global(h3:first-child) {
  margin-top: 0;
}

.markdown :global(h1) {
  font-size: 18px;
}

.markdown :global(h2) {
  font-size: 16px;
}

.markdown :global(h3) {
  font-size: 15px;
}

/*  */
.markdown :global(hr) {
  border: none;
  border-top: 1px solid rgba(55, 53, 47, 0.1);
  margin: 16px 0;
}

/*  */
.markdown :global(table) {
  border-collapse: collapse;
  width: 100%;
  margin: 12px 0;
  font-size: 14px;
}

.markdown :global(th),
.markdown :global(td) {
  border: 1px solid rgba(55, 53, 47, 0.1);
  padding: 8px 12px;
  text-align: left;
}

.markdown :global(th) {
  background: rgba(55, 53, 47, 0.04);
  font-weight: 600;
}

.markdown :global(tr:hover td) {
  background: rgba(55, 53, 47, 0.02);
}
