/*  -  */

/* ============  ============ */
.panel {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /*  */
  background: linear-gradient(180deg, #F1F1E8 0%, #E8F0E8 100%);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  animation: slideDown 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: transform;
}

@keyframes slideDown {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}

.panel.closing {
  animation: none;
}

/* ============  ============ */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  padding-top: calc(12px + env(safe-area-inset-top));
  background-color: transparent;
  flex-shrink: 0;
}

.backButton {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background-color: transparent;
  color: rgb(55, 53, 47);
  cursor: pointer;
  border-radius: 12px;
  transition: all 0.2s;
  margin-left: -8px;
}

.backButton:hover {
  background-color: rgba(55, 53, 47, 0.06);
}

.backButton:active {
  transform: scale(0.95);
}

.headerTitle {
  font-size: 17px;
  font-weight: 600;
  color: rgb(55, 53, 47);
  flex: 1;
  text-align: center;
}

.headerPlaceholder {
  width: 40px;
  height: 40px;
  margin-right: -8px;
}

/* ============ DEBUG  ============ */
.debugButton {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background-color: transparent;
  color: rgba(55, 53, 47, 0.4);
  cursor: pointer;
  border-radius: 12px;
  transition: all 0.2s;
  margin-right: -8px;
}

.debugButton:hover {
  background-color: rgba(55, 53, 47, 0.06);
  color: rgba(55, 53, 47, 0.6);
}

.debugButton:active {
  transform: scale(0.95);
}

.debugButton.debugActive {
  color: #FF6B35;
  background-color: rgba(255, 107, 53, 0.1);
}

/* ============  ============ */
.content {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

/* ============  -  ============ */
.levelBadge {
  min-width: 32px;
  position: absolute;
  top: 16px;
  right: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 4px;
  padding: 12px 16px;
  border-radius: 16px;
  /*  -  */
  background: linear-gradient(128deg, rgba(200, 220, 200, 0.45) 0%, rgba(180, 200, 175, 0.55) 100%);
  box-shadow: 0 2px 8px rgba(160, 180, 160, 0.15);
  backdrop-filter: blur(8px);
  z-index: 5;
  animation: fadeInRight 0.5s ease-out 0.2s both;
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* ============  -  ============ */
.spiritJadeBadge {
  position: absolute;
  top: 16px;
  left: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  border-radius: 16px;
  background: linear-gradient(128deg, rgba(200, 220, 200, 0.45) 0%, rgba(180, 200, 175, 0.55) 100%);
  box-shadow: 0 2px 8px rgba(160, 180, 160, 0.15);
  backdrop-filter: blur(8px);
  z-index: 5;
  animation: fadeInLeft 0.5s ease-out 0.2s both;
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.spiritJadeIcon {
  width: 24px;
  height: 24px;
}

.spiritJadeValue {
  font-size: 16px;
  font-weight: 700;
  color: rgb(91 88 78);
}

.levelName {
font-size: 18px;
    font-weight: 700;
    color: rgb(91 88 78);
    width: 15px;
    margin: 24px auto;
}

.levelStage {
  font-size: 12px;
  font-weight: 500;
  color: rgba(55, 53, 47, 0.7);
}

/* ============  -  ============ */
.characterSection {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 16px;
  position: relative;
}

.characterImage {
  width: 374px;
  max-width: 640px;
  height: auto;
  max-height: 55vh;
  object-fit: contain;
  animation: fadeInScale 0.6s ease-out 0.1s both, floatBreathing 3s ease-in-out infinite 0.7s;
  transition: opacity 0.4s ease-out, transform 0.4s ease-out;
}

/*  */
@keyframes floatBreathing {
  0%, 100% {
    transform: translateY(0) scale(1);
  }
  50% {
    transform: translateY(-8px) scale(1.02);
  }
}

/*  */
.characterImage.imageTransitioning {
  opacity: 0;
  transform: scale(0.95);
}

@keyframes fadeInScale {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* ============ DEBUG  ============ */
.debugPanel {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  padding: 16px 24px;
  background: rgba(255, 107, 53, 0.95);
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(255, 107, 53, 0.3);
  z-index: 10;
  animation: fadeInUp 0.3s ease-out;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

.debugInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
}

.debugLabel {
  font-size: 11px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
  letter-spacing: 1px;
}

.debugLevelName {
  font-size: 16px;
  font-weight: 700;
  color: #fff;
}

.debugIndex {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.6);
}

.debugControls {
  display: flex;
  gap: 12px;
}

.debugNavButton {
  padding: 8px 16px;
  border: none;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  font-size: 13px;
  font-weight: 600;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
}

.debugNavButton:hover {
  background: rgba(255, 255, 255, 0.3);
}

.debugNavButton:active {
  transform: scale(0.95);
}

/* ============  -  ============ */
.progressSection {
  padding: 24px 82px;
  background: transparent;
  position: relative;
  top: -100px;
}

.progressBar {
  height: 8px;
  background-color: rgba(55, 53, 47, 0.1);
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 12px;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.05);
}

.progressFill {
  height: 100%;
  border-radius: 4px;
  transition: width 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.progressInfo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.progressText {
  font-size: 14px;
  font-weight: 500;
  color: rgba(55, 53, 47, 0.6);
}

/* ============  ============ */
.footer {
  flex-shrink: 0;
  padding: 16px 24px 24px;
  padding-bottom: calc(24px + env(safe-area-inset-bottom));
  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}

/*  */
.breakthroughButton {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  width: 100%;
  max-width: 280px;
  padding: 14px 24px;
  border: none;
  border-radius: 16px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.breakthroughButton.active {
  background: linear-gradient(135deg, #8B7355 0%, #A68B5B 100%);
  color: #fff;
  box-shadow: 0 4px 16px rgba(139, 115, 85, 0.3);
}

.breakthroughButton.active:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(139, 115, 85, 0.4);
}

.breakthroughButton.active:active {
  transform: translateY(0);
}

.breakthroughButton.disabled {
  background: rgba(55, 53, 47, 0.08);
  color: rgba(55, 53, 47, 0.4);
  cursor: not-allowed;
}

.breakthroughButton svg {
  width: 18px;
  height: 18px;
}

/*  */
.seclusionBanner {
  display: flex;
  align-items: center;
  gap: 12px;
  width: 100%;
  max-width: 320px;
  padding: 12px 16px;
  background: rgba(255, 171, 64, 0.15);
  border: 1px solid rgba(255, 171, 64, 0.3);
  border-radius: 12px;
}

.seclusionIcon {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FF9800;
}

.seclusionInfo {
  flex: 1;
}

.seclusionTitle {
  font-size: 14px;
  font-weight: 600;
  color: rgb(55, 53, 47);
}

.seclusionDesc {
  font-size: 12px;
  color: rgba(55, 53, 47, 0.6);
  margin-top: 2px;
}

.swipeHint {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 18px;
  color: rgb(55 53 47 / 15%);
  font-size: 13px;
}

.swipeHint::before {
  content: '';
  width: 32px;
  height: 1.5px;
  background: rgb(106 104 98 / 16%);
  border-radius: 2px;
}
.swipeHint::after {
  content: '';
  width: 32px;
  height: 1.5px;
  background: rgb(106 104 98 / 16%);
  border-radius: 2px;
}

/* ============  ============ */
@media (min-width: 768px) {
  .characterImage {
    max-width: 600px;
    max-height: 50vh;
  }
  
  .levelBadge {
    top: 24px;
    right: 40px;
  }
  
  .progressSection {
    max-width: 480px;
    margin: 0 auto;
    width: 100%;
  }
}

/* ============  ============ */
@media (prefers-reduced-motion: reduce) {
  .panel {
    animation: none;
  }
  
  .characterImage,
  .levelBadge {
    animation: none;
    opacity: 1;
    transform: none;
  }
  
  .progressFill {
    transition: none;
  }
  
  .backButton {
    transition: none;
  }
}

