/*  - Notion  + Soft UI Evolution */

/* ============================================
   
   ============================================ */
.container {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  margin: 12px 16px;
  overflow: hidden;
}

/*  */
.multiContainer {
  max-height: 60vh;
  display: flex;
  flex-direction: column;
}

/*  */
.locked {
  opacity: 0.45;
  pointer-events: none;
}

/* ============================================
   
   ============================================ */
.multiHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 16px 8px;
  flex-shrink: 0;
}

.multiHeaderTitle {
  font-size: 14px;
  font-weight: 500;
  color: rgba(55, 53, 47, 0.65);
}

/*  */
.progressIndicator {
  font-size: 12px;
  font-weight: 500;
  color: rgba(55, 53, 47, 0.5);
  background: rgba(55, 53, 47, 0.04);
  padding: 4px 10px;
  border-radius: 12px;
}

/* ============================================
   
   ============================================ */
.questionList {
  flex: 1;
  overflow-y: auto;
  padding: 8px 16px 14px;
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: contain;
}

/*  */
.questionCard {
  margin-bottom: 20px;
}

.questionCard:last-child {
  margin-bottom: 0;
}

/*  */
.questionTitle {
  font-size: 14px;
  font-weight: 500;
  line-height: 1.4;
  color: rgba(55, 53, 47, 0.65);
  margin-bottom: 10px;
}

/* ============================================
   
   ============================================ */
.header {
  padding: 14px 16px 10px;
}

.title {
  font-size: 14px;
  font-weight: 500;
  color: rgba(55, 53, 47, 0.65);
  line-height: 1.4;
}

/* ============================================
   
   ============================================ */
.optionList {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid rgba(55, 53, 47, 0.08);
  background: rgba(255, 255, 255, 0.6);
}

/*  */
.optionItem {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 14px 16px;
  background: transparent;
  border: none;
  border-top: 1px solid rgba(55, 53, 47, 0.06);
  cursor: pointer;
  transition: background-color 0.15s ease;
  text-align: left;
}

.optionItem:first-child {
  border-top: none;
}

.optionItem:hover:not(:disabled) {
  background: rgba(55, 53, 47, 0.03);
}

.optionItem:active:not(:disabled) {
  transform: scale(0.99);
}

.optionItem:disabled {
  cursor: not-allowed;
}

/*  -  */
.optionSelected {
  background: linear-gradient(135deg, #F1F1E8 0%, #E8F0E8 100%);
}

.optionSelected:hover:not(:disabled) {
  background: linear-gradient(135deg, #F1F1E8 0%, #E8F0E8 100%);
}

/* ============================================
   
   ============================================ */
.optionBadge {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  border-radius: 8px;
  background: rgba(55, 53, 47, 0.06);
  font-size: 12px;
  font-weight: 600;
  color: rgba(55, 53, 47, 0.5);
  flex-shrink: 0;
  transition: all 0.15s ease;
}

/*  */
.badgeSelected {
  background: linear-gradient(135deg, #8B9D83 0%, #7A8C72 100%);
  color: #FFFFFF;
}

/* ============================================
   
   ============================================ */
.optionText {
  flex: 1;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.4;
  color: rgb(55, 53, 47);
}

/* ============================================
   
   ============================================ */
.submitWrapper {
  padding: 12px 16px 16px;
  border-top: 1px solid rgba(55, 53, 47, 0.06);
  background: rgba(255, 255, 255, 0.98);
  flex-shrink: 0;
}

.submitButton {
  width: 100%;
  height: 48px;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1;
  cursor: pointer;
  transition: all 0.2s ease;
  /*  */
  background: rgba(55, 53, 47, 0.08);
  color: rgba(55, 53, 47, 0.35);
}

.submitButton:disabled {
  cursor: not-allowed;
}

/*  */
.submitEnabled {
  background: rgb(55, 53, 47);
  color: #FFFFFF;
  box-shadow: 0 2px 8px rgba(55, 53, 47, 0.2);
}

.submitEnabled:hover:not(:disabled) {
  opacity: 0.9;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(55, 53, 47, 0.25);
}

.submitEnabled:active:not(:disabled) {
  transform: translateY(0);
  box-shadow: 0 2px 8px rgba(55, 53, 47, 0.2);
}

/* ============================================
   
   ============================================ */

/*  */
.customPlaceholder {
  color: rgba(55, 53, 47, 0.45);
}

/*  -  */
.inlineInput {
  flex: 1;
  border: none;
  background: transparent;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.4;
  color: rgb(55, 53, 47);
  outline: none;
  padding: 0;
  margin: 0;
}

.inlineInput::placeholder {
  color: rgba(55, 53, 47, 0.35);
}

/* ============================================
    - +
   ============================================ */
.submittedContainer {
  background: rgba(255, 255, 255, 0.9);
  padding: 14px 16px;
}

/*  */
.submittedHeader {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 12px;
}

.submittedIcon {
  font-size: 16px;
  line-height: 1;
}

.submittedTitle {
  font-size: 14px;
  font-weight: 500;
  color: rgba(55, 53, 47, 0.65);
}

/*  */
.submittedList {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/*  */
.submittedItem {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

/*  */
.submittedQuestion {
  font-size: 13px;
  font-weight: 400;
  color: rgba(55, 53, 47, 0.65);
  line-height: 1.4;
}

/*  -  */
.submittedAnswer {
  font-size: 15px;
  font-weight: 500;
  color: #7A8C72;
  line-height: 1.4;
}
