/* ====================  ==================== */
.panel {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  /*  */
  background: transparent;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  /*  transform */
  /*  */
  pointer-events: none;
}

.panelVisible {
  /*  */
}

.panelExiting {
  /*  */
}

/* ====================  ==================== */
.pageStack {
  position: relative;
  flex: 1;
  overflow: hidden;
  perspective: 1000px;
  pointer-events: none;
}

/* ====================  ==================== */
.pageLayer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #f7f7f5;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transition: transform 0.4s ease-out, box-shadow 0.4s ease-out, opacity 0.4s ease-out;
  will-change: transform, box-shadow, opacity;
  /*  */
  transform: translateX(100%);
  /*  */
  pointer-events: auto;
}

/*  */
.pageLayerActive {
  transform: translateX(0);
  z-index: 2;
  box-shadow: -4px 0 20px rgba(0, 0, 0, 0.15);
}

/*  -  */
.pageLayerBackground {
  transform: scale(0.95) translateX(-5%);
  z-index: 1;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  pointer-events: none;
}

/*  -  */
.pageLayerEntering {
  z-index: 2;
  /*  transition animation  */
  transition: none;
  animation: slideInFromRight 0.4s ease-out forwards;
}

/*  -  */
.pageLayerExiting {
  z-index: 2;
  /*  transition animation  */
  transition: none;
  animation: slideOutToRight 0.4s ease-in forwards;
}

/*  */
.pageLayerHidden {
  transform: scale(0.9) translateX(-10%);
  z-index: 0;
  opacity: 0;
  pointer-events: none;
}

/* ====================  ==================== */
@keyframes slideInFromRight {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
    box-shadow: -4px 0 20px rgba(0, 0, 0, 0.15);
  }
}

@keyframes slideOutToRight {
  from {
    transform: translateX(0);
    box-shadow: -4px 0 20px rgba(0, 0, 0, 0.15);
  }
  to {
    transform: translateX(100%);
    box-shadow: none;
  }
}

/* ====================  ==================== */
.header {
  padding: 12px 16px;
  padding-top: calc(12px + env(safe-area-inset-top));
  /*  */
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-shrink: 0;
}

/*  */
.backButton {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  border: none;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: rgb(55, 53, 47);
  transition: all 0.2s;
  margin-left: -8px;
}

.backButton:hover {
  background: rgba(55, 53, 47, 0.06);
}

.backButton:active {
  transform: scale(0.95);
}

/*  */
.title {
  margin: 0;
  font-size: 17px;
  font-weight: 600;
  color: rgb(55, 53, 47);
  flex: 1;
  text-align: center;
}

/*  */
.headerSpacer {
  width: 40px;
  height: 40px;
  margin-right: -8px;
}

/*  */
.closeButton {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  border: none;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: rgba(55, 53, 47, 0.65);
  transition: all 0.2s;
}

.closeButton:hover {
  background: rgba(55, 53, 47, 0.06);
  color: rgb(55, 53, 47);
}

.closeButton:active {
  transform: scale(0.95);
}

/* ====================  ==================== */
.content {
  flex: 1;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  position: relative;
}

/* ====================  ==================== */
@media (prefers-reduced-motion: reduce) {
  .pageLayer {
    animation: none !important;
    transition: none !important;
  }
  
  .pageLayerActive {
    transform: translateX(0);
  }
  
  .pageLayerBackground {
    transform: scale(0.95) translateX(-5%);
  }
  
  .pageLayerEntering {
    transform: translateX(0);
  }
  
  .pageLayerExiting {
    transform: translateX(100%);
  }
}

/* ====================  ==================== */
@media (min-width: 768px) {
  .panel {
    /*  */
    background: rgba(0, 0, 0, 0.3);
  }
  
  .pageStack {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 420px;
    height: 85vh;
    max-height: 700px;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  }
  
  .pageLayer {
    border-radius: 16px;
  }
}



