/*  -  */

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0;
  box-sizing: border-box;
}

/*  */
.small {
  --cup-scale: 0.8;
}

.medium {
  --cup-scale: 1;
}

.large {
  --cup-scale: 1.25;
}

/*  */
.cupContainer {
  width: 330px;
    height: 250px;
  max-width: 100%;
  display: flex;
      margin-top: 70px;
    margin-left: 42px;
  align-items: center;
  justify-content: center;
}

/*  SVG */
.cupSvg {
  width: 100%;
  height: 100%;
}

/*  */
.liquid {
  /*  framer-motion  */
}

/*  */
.waveOverlay {
  mix-blend-mode: overlay;
}

/*  - Apple Music  */
.fluidOverlay {
  mix-blend-mode: soft-light;
  pointer-events: none;
}

/*  */
.energyGlow {
  mix-blend-mode: screen;
  pointer-events: none;
  filter: blur(2px);
}

/*  */
.progressInfo {
  /* margin-top: 24px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin-bottom: 24px;
}

/*  */
.mainValue {
  font-size: 48px;
  font-weight: 100;
  color: #1a1a1a;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  line-height: 1;
}

/*  */
.subInfo {
  font-size: 16px;
  color: #999;
  font-weight: 200;
}

/*  */
@media (prefers-reduced-motion: reduce) {
  .liquid,
  .waveOverlay,
  .fluidOverlay,
  .energyGlow {
    animation: none !important;
    transition: none !important;
  }
}

