/* CreateTaskModal  - Popup  +  */

/* ==================== Popup  ==================== */
.popupBody {
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  height: 85vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background: #f7f7f5;
}

/* ====================  Header ==================== */
.header {
  padding: 12px 16px;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-shrink: 0;
}

.headerButton {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  border: none;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: rgb(55, 53, 47);
  transition: all 0.2s;
  margin-left: -8px;
}

.headerButton:hover {
  background: rgba(55, 53, 47, 0.06);
}

.headerButton:active {
  transform: scale(0.95);
}

.title {
  margin: 0;
  font-size: 17px;
  font-weight: 600;
  color: rgb(55, 53, 47);
  flex: 1;
  text-align: center;
}

.headerSpacer {
  width: 40px;
  height: 40px;
  margin-right: -8px;
}

/* ====================  ==================== */
.contentWrapper {
  position: relative;
  flex: 1;
  overflow: hidden;
}

/* ====================  ==================== */
.pageStack {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background: #f7f7f5;
}

/* ====================  ==================== */
.pageLayer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #f7f7f5;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  /*  */
  visibility: hidden;
  transform: translateX(100%);
  /*  transition  */
  transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94),
              visibility 0s linear 0.3s;
  will-change: transform;
}

/*  -  */
.pageLayerActive {
  visibility: visible;
  transform: translateX(0);
  z-index: 2;
  transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94),
              visibility 0s linear 0s;
}

/*  -  30%  */
.pageLayerBackground {
  visibility: visible;
  transform: translateX(-30%);
  z-index: 1;
  pointer-events: none;
  transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94),
              visibility 0s linear 0s;
}

/*  -  */
.pageLayerEntering {
  visibility: visible;
  transform: translateX(0);
  z-index: 3;
  box-shadow: -4px 0 16px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94),
              visibility 0s linear 0s;
}

/*  -  */
.pageLayerShrinking {
  visibility: visible;
  transform: translateX(-30%);
  z-index: 1;
  pointer-events: none;
  transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94),
              visibility 0s linear 0s;
}

/*  -  */
.pageLayerExiting {
  visibility: visible;
  transform: translateX(100%);
  z-index: 3;
  box-shadow: -4px 0 16px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94),
              visibility 0s linear 0s;
}

/*  -  */
.pageLayerExpanding {
  visibility: visible;
  transform: translateX(0);
  z-index: 1;
  pointer-events: none;
  transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94),
              visibility 0s linear 0s;
}

/*  */
.pageLayerHidden {
  visibility: hidden;
  transform: translateX(-50%);
  z-index: 0;
}

/* ====================  ==================== */
@media (prefers-reduced-motion: reduce) {
  .pageLayer,
  .pageLayerActive,
  .pageLayerBackground,
  .pageLayerEntering,
  .pageLayerShrinking,
  .pageLayerExiting,
  .pageLayerExpanding {
    transition: none !important;
  }
}

/* ==================== AI  -  ==================== */
.aiModeContainer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background: #ffffff;
  /*  */
  transform: translateY(100%);
  opacity: 0;
  visibility: hidden;
  z-index: 10;
  transition: transform 0.35s cubic-bezier(0.32, 0.72, 0, 1),
              opacity 0.25s ease-out,
              visibility 0s linear 0.35s;
}

/* AI  */
.aiModeVisible {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
  transition: transform 0.35s cubic-bezier(0.32, 0.72, 0, 1),
              opacity 0.2s ease-out,
              visibility 0s linear 0s;
}


/* ====================  ==================== */
@media (min-width: 768px) {
  .popupBody {
    max-width: 420px;
    margin: 0 auto;
    max-height: 700px;
  }
}
